import React, { useMemo } from "react";
import {
  Card,
  CardContent,
  CardHeader,
  makeStyles,
  Typography,
} from "@material-ui/core";
import { CardProps } from "@material-ui/core/Card/Card";
import {
  TBorrowedAssetState,
  TSuppliedAssetState,
} from "../networkOverview/NetworkSupplyBar";
import { TAssetInMarketParameters } from "../../../../services/lendingNetworksService/ILendingNetworksService";
import {
  BACKGROUND_DARK_COLOR,
  OLA_CYAN,
  TEXT_ON_DARK,
} from "../../../../theme/themeConstants";
import { DualCompletionCircle } from "../../../../components/balances/DualCompletionCircle";
import { useCTokenStoreByBaseAsset } from "../../../../store/storeHooks";
import { observer } from "mobx-react";
import { numericalDisplay } from "../../../../components/displayCalculations/displayCalculations";
import { SYMBOLS_TO_COLORS } from "../../../../constants/assetsDisplay";
import clsx from "clsx";

interface IProps {
  underlyingAddress: string;
  isActive?: boolean;
  shouldDim?: boolean;
}

// interface IProps
//     extends TBorrowedAssetState,
//         TSuppliedAssetState,
//         TAssetInMarketParameters {
// }

const useStyles = makeStyles((theme) => ({
  cardContainer: {
    transition: "300ms",
    borderRadius: "10%",
    boxSizing: "border-box",
  },
  activeCard: {
    backgroundColor: BACKGROUND_DARK_COLOR,
    color: TEXT_ON_DARK,
    border: `5px solid ${OLA_CYAN}`,
  },
  semiHidden: {
    filter: "opacity(0.85)",
  },
  cardHeader: {
    paddingTop: "0.5rem",
    // textAlign: "center",
    fontWeight: "bold",
    // color: TEXT_ON_LIGHT,
    // paddingTop: "0.5rem",
    // paddingBottom: "0.5rem",
    // paddingLeft: "0.5rem",
    // paddingRight: "0.5rem",
    display: "flex",
    flexDirection: "row",
    width: "100%",
    justifyContent: "center",
    alignItems: "center",
    textAlign: "center",
    // backgroundColor: BACKGROUND_DARK_COLOR,
    // color: TEXT_ON_DARK,
  },
}));

export const AssetViewCard = observer<React.FC<IProps & CardProps>>((props) => {
  const classes = useStyles();
  const {
    underlyingAddress,
    isActive,
    shouldDim,
    className: classNameForContainer,
    ...restCardProps
  } = props;
  const cTokenStore = useCTokenStoreByBaseAsset(underlyingAddress);

  const symbol = cTokenStore?.underlyingSymbol || "Loading";
  const totalSupplyBalanceUsd = cTokenStore?.totalSupplyBalanceUsd || 0;
  const totalBorrowBalanceUsd = cTokenStore?.totalBorrowBalanceUsd || 0;
  const underlyingUsdPrice = cTokenStore?.underlyingUsdPrice || 0;

  const usagePercentage = (totalBorrowBalanceUsd / totalSupplyBalanceUsd) * 100;

  const usagePercentageForDisplay = numericalDisplay(usagePercentage, 4);
  const totalBorrowBalanceUsdForDisplay = numericalDisplay(
    totalBorrowBalanceUsd,
    4
  );
  const underlyingUsdPriceForDisplay = numericalDisplay(underlyingUsdPrice, 4);

  // @ts-ignore
  const colorForAsset = SYMBOLS_TO_COLORS[symbol] || "black";

  const classesForContainer = useMemo(() => {
    const classNames = [classes.cardContainer, classNameForContainer];

    if (isActive) {
      classNames.push(classes.activeCard);
    } else if (shouldDim) {
      classNames.push(classes.semiHidden);
    }

    return classNames;
  }, [
    classNameForContainer,
    classes.activeCard,
    classes.cardContainer,
    classes.semiHidden,
    isActive,
    shouldDim,
  ]);

  return (
    <Card {...restCardProps} className={clsx(classesForContainer)}>
      <div className={classes.cardHeader}>
        <Typography variant={"h6"}>
          {symbol} (${underlyingUsdPriceForDisplay}$)
        </Typography>
      </div>
      <CardContent>
        <DualCompletionCircle
          baseTitle={`${symbol} borrowed`}
          baseMessage={`${totalBorrowBalanceUsdForDisplay}$`}
          activeTitle={`${symbol} Utilization`}
          activeMessage={`${usagePercentageForDisplay}%`}
          primaryCompletionPercentage={100}
          secondaryCompletionPercentage={usagePercentage}
        />
      </CardContent>
    </Card>
  );
});
