import { observer } from "mobx-react";
import React from "react";
import { NetworkOverview } from "./networkOverview/NetworkOverview";
import { AssetsViewSection } from "./assetsView/AssetsViewSection";
import { useStateful } from "react-hanger";

interface IProps {}

export const NetworkTab = observer<React.FunctionComponent<IProps>>((props) => {
  const activeAssetSymbolWrapper = useStateful("");

  return (
    <>
      <NetworkOverview
        activeAssetSymbol={activeAssetSymbolWrapper.value}
        setActiveAssetSymbol={activeAssetSymbolWrapper.setValue}
      />
      <AssetsViewSection
        activeAssetSymbol={activeAssetSymbolWrapper.value}
        setActiveAssetSymbol={activeAssetSymbolWrapper.setValue}
      />
    </>
  );
});
